import fetch from 'isomorphic-unfetch';
import Link from 'next/link';
import { request } from 'graphql-request'

const Index = props => (
  <div>
    <h1>Title</h1>
    <ul>
      {props.shows.map(({ show }) => (
        <li key={show.id}>
          <Link as={`/p/${show.id}`} href={`/post?id=${show.id}`}>
            <a>{show.name}</a>
          </Link>
        </li>
      ))}
    </ul>
  </div>
);

