Index.getInitialProps = async function() {
  const query = `{
    allRoutes {
      routeName
    }
  }`
  const res = await request('https://server-dubymzzcjd.now.sh/graphql');
  const data = await res.json();
  console.log(`Show data fetched. Count: ${data.length}`);
  return { shows: data };
};

export default Index;



